
body{
  background-color: #a5d018;
}
.container1 {
  max-width: 800px;
  margin: 20px auto;
  padding: 30px;
  width: 80%;
  background-color: rgb(251, 41, 22);
  border: 20px solid rgb(255, 255, 255); /* Added border property for outlining */
  border-radius: 15px; /* Curved borders on all four edges */
}


.container2 {
  max-width: 800px;
  margin: 20px auto;
  padding: 30px;
  width: 80%;
  background-color: rgba(12, 236, 57, 0.896);
  border: 20px solid rgb(253, 61, 3); /* Added border property for outlining */
  border-radius: 15px; /* Curved borders on all four edges */
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* Adjust minmax value */
  gap: 20px;
}

/* Styles for cards in container1 */
.container1 .card {
  border: 4px solid #7606f6;
  border-radius: 10px;
  overflow: hidden;
  border-radius: 15px;
  background-color: rgb(16, 222, 122); /* Change the background color */
  box-shadow: 0 4px 8px rgba(20, 8, 245, 0.816);
  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.container1 .card:hover {
  box-shadow: 0 6px 12px rgba(234, 11, 11, 0.777);
  transform: translateY(-3px);
}

/* Styles for cards in container2 */
.container2 .card {
  border: 4px solid #ed1404;
  border-radius: 10px;
  overflow: hidden;
  border-radius: 15px;
  background-color: rgb(235, 160, 156); /* Change the background color */
  box-shadow: 0 4px 8px rgb(35, 4, 160);
  transition: box-shadow 5ms ease-in-out, transform 0.3s ease-in-out;
}

.container2 .card:hover {
  box-shadow: 0 12px 20px rgba(86, 6, 191, 0.999);
  transform: translateY(-10px);
  
}

.card-body {
  padding: 10px;
}

.card-title {
  font-size: 18px;
  margin-bottom: 5px;
}

.card-text {
  font-size: 14px;
  margin-bottom: 5px;
}
.container2 {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  width: 80%;
  background-color: rgba(12, 236, 57, 0.896);
  border: 4px solid rgb(253, 61, 3);
  border-radius: 15px;
  perspective: 100px; /* Added perspective for 3D effect */
}


.container2 .card {
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.container2 .card:hover {
  transform: rotateY(10deg); /* Adjust the rotation angle as needed */
}

